import {
  fetchUrl,
  formatDuration,
  formatTimestampToAMPM,
  getAndUpdateTime,
  getToken,
  logout,
  redirectTo,
  removeOptions,
  saveToken
} from "../util";
import { closeModal, closeModalAuto, openModal, openModalAuto } from "../util/modal";
import {
  XANO_COACH_ALL_GYMS_URL,
  XANO_COACH_DASHBOARD_URL,
  XANO_COACH_SESSION_DETAILS_REDIRECT,
  XANO_COACH_SIGN_IN_REDIRECT,
  XANO_COACH_START_SESSION_URL,
} from "../util/xano_urls";

let ALL_GYM_LOCATIONS = [];

window.onload = function () {
  const logoutButton = document.getElementById("navbar-logout-btn");
  if (logoutButton) {
    logoutButton.onclick = () => {
      logout("/coach/sign-in");
    };
  }

 const token = getToken();
  if (token) {
    getAndUpdateTime();
    forceSelectGym(token);
  } else {
    // ? if there is no token
    redirectTo(`${XANO_COACH_SIGN_IN_REDIRECT}?reason=NOT_SIGNED_IN`);
  }
};

async function forceSelectGym(token, alreadyChangedGym = false) {
  let coachSelectedGymIdCookie = getToken("nammaxCoachSelectedGym");
  // ? if coach is selecting gym for the first time in the day
  // ? or if the coach has already changed their gym and wants to change it again
  if (!coachSelectedGymIdCookie || alreadyChangedGym) {
    openModalAuto({ _modalWindow: "gym-select-popout" });
    let data;

    if (ALL_GYM_LOCATIONS.length === 0) {
      data = await fetchUrl(XANO_COACH_ALL_GYMS_URL, "GET", false, token);
    } else {
      data = ALL_GYM_LOCATIONS;
    }

    if (data.isFetchOk !== undefined && !data.isFetchOk) {
      console.error(data.message);
      redirectTo(`${XANO_COACH_SIGN_IN_REDIRECT}?reason=NOT_SIGNED_IN`);
      return;
    }

    ALL_GYM_LOCATIONS = data;

    const coachGymSelectPopout = document.getElementById("gym-select-popout");
    const selectGymEl = coachGymSelectPopout.querySelector("#coach-gym-select");
    const confirmGymSelectBtn = coachGymSelectPopout.querySelector("#confirm-gym-select-btn");

    if (alreadyChangedGym) {
      // ? show cancel button
      const cancelGymSelectBtn = coachGymSelectPopout.querySelector("#cancel-gym-select-btn");

      cancelGymSelectBtn.onclick = () => {
        closeModalAuto({ _modalWindow: "gym-select-popout" });
      };

      cancelGymSelectBtn.classList.remove("hide");
    }

    removeOptions(selectGymEl);
    data.forEach((gym, indx) => {
      var opt = document.createElement("option");
      opt.value = gym.id;
      opt.innerText = gym.location;
      if (indx === 0) opt.selected = true;
      // ? == instead of ===, because cookie is a string and id is an integer
      if (coachSelectedGymIdCookie == gym.id) opt.selected = true;
      selectGymEl.appendChild(opt);
    });

    confirmGymSelectBtn.onclick = () => {
      let gymId = selectGymEl.value;
      getSessions(gymId, token);
      closeModalAuto({ _modalWindow: "gym-select-popout" }, () => {
        saveToken(gymId, "nammaxCoachSelectedGym");
      });
    };
  } else {
    getSessions(coachSelectedGymIdCookie, token);
  }
}

async function getSessions(gymId, token) {
  let res = await fetchUrl(XANO_COACH_DASHBOARD_URL + gymId, "GET", false, token);

  if (res.isFetchOk !== undefined && !res.isFetchOk) {
    console.error(res.message);
    // redirectTo(`${SIGN_IN_URL}?reason=NOT_SIGNED_IN`);
    return;
  }

  const { coach, sessions: sessionsData } = res;

  document.getElementById("user-name").innerText = "Coach " + coach.name;
  document.getElementById("location-map-name").innerText = coach.location;
  // ? if coach currently has an active session, redirect to that session
  sessionsData.forEach((session) => {
    if (session.status === "IN PROGRESS" && session.coach.status === "IN SESSION" && session.team_id === coach.id) {
      redirectTo(`${XANO_COACH_SESSION_DETAILS_REDIRECT}?session=${session.id}`);
      return;
    }
  });

  initReselectGym(token);
  appendSessions(token, sessionsData);
}

function initReselectGym(token) {
  openModal(
    {
      _modalWindow: "gym-select-popout",
      TriggerBtn: "location-map-change-btn",
      baseElement: document.getElementById("location-map-container"),
    },
    () => {
      forceSelectGym(token, true);
    }
  );
  closeModal({ _modalWindow: "gym-select-popout", TriggerBtn: "cancel-gym-select-btn" });
}

async function appendSessions(token, sessions) {
  let sessionsWrapper = document.getElementById("view-coach-sessions-container");
  const og_webflow_element = sessionsWrapper.querySelector("#view-coach-sessions-item");

  function appendSessionsFn(_data) {
    let allItems = sessionsWrapper.querySelectorAll(".session-details-w.card-item");
    allItems.forEach((item, indx) => {
      if (indx !== allItems.length - 1) item.remove();
    });

    let noSessionsFound = sessionsWrapper.querySelector("#no-sessions-found");

    if (_data.length === 0) noSessionsFound.style.display = "flex";
    else noSessionsFound.style.display = "none";

    _data.forEach((session) => {
      // Copy the original webflow element
      const clonedCard = og_webflow_element.cloneNode(true);
      clonedCard.setAttribute("id", "");

      clonedCard.querySelector("#session-name").innerText = session.session_name;
      // clonedCard.querySelector("#date").innerText = getDateFromTimestamp(session.time);
      clonedCard.querySelector("#time").innerText = formatTimestampToAMPM(session.time, true);
      clonedCard.querySelector("#duration").innerText = formatDuration(session.duration);
      clonedCard.querySelector("#session-slot-bar").style.width = (session.users.length / session.capacity) * 100 + "%";
      clonedCard.querySelector("#session-capacity").innerText = `${session.users.length}/${session.capacity}`;

      // ? coach clicks on start session, which will then display a popup
      openModal(
        {
          baseElement: clonedCard,
          TriggerBtn: "start-session-btn",
          _modalWindow: "confirm-session-popout",
        },
        () => {
          const confirmSessionPopout = document.getElementById("confirm-session-popout");

          confirmSessionPopout.querySelector("#session-name").innerText = session.session_name;
          confirmSessionPopout.querySelector("#time").innerText = formatTimestampToAMPM(session.time, true);
          confirmSessionPopout.querySelector("#duration").innerText = formatDuration(session.duration);
          confirmSessionPopout.querySelector("#gym_location").innerText = session.location;
          confirmSessionPopout.querySelector(
            "#session-capacity"
          ).innerText = `${session.users.length}/${session.capacity}`;
          confirmSessionPopout.querySelector("#current-time").innerText = formatTimestampToAMPM(new Date(), true);

          // ? when coach clicks confirm start session btn
          const confirmStartSessionPopoutBtn = confirmSessionPopout.querySelector("#confirm-start-session-popout-btn");
          confirmStartSessionPopoutBtn.onclick = async () => {
            // ? coach id is already being assigned to the session via its auth id from xano
            // ? therefore, we do not need to add coach_id to the xano_input
            const xano_input = {};
            const confirmData = await fetchUrl(XANO_COACH_START_SESSION_URL + session.id, "POST", xano_input, token);

            if (confirmData.isFetchOk !== undefined && !confirmData.isFetchOk) {
              let warning_block = confirmSessionPopout.querySelector("#warning_block");
              warning_block.innerText = confirmData.message;
              warning_block.style.display = "flex";
              return;
            }

            redirectTo(`${XANO_COACH_SESSION_DETAILS_REDIRECT}?session=${session.id}`);
          };
        }
      );
      clonedCard.style.display = "flex"; // ? required
      sessionsWrapper.prepend(clonedCard);
    });
  }

  appendSessionsFn(sessions);
  closeModal(
    {
      TriggerBtn: "close-cancel-session-modal-btn",
      _modalWindow: "confirm-session-popout",
    },
    () => {
      const confirmSessionPopout = document.getElementById("confirm-session-popout");
      let warning_block = confirmSessionPopout.querySelector("#warning_block");
      warning_block.style.display = "none";
    }
  );
  og_webflow_element.style.display = "none";
}

var Webflow = Webflow || [];
Webflow.push(function () {
  // unbind webflow form handling (keep this if you only want to affect specific forms)
  $(document).off("submit");
  /* Any form on the page */

  $("form").on("submit", async function (e) {
    e.preventDefault();

    // ? this is required to prevent webflow from submitting the form
    // ? It doesnt need to do anything, since I'm only setting document.cookie, not submitting
  });
});
